<template>
    <div>
        <div class="floor" v-for="(i,k) in form">
            <div class="container floor-item" style="padding-left: 0">
                <div class="tile">
                    {{i.name}}
                    <img src="../assets/img/title.png" alt="">
                </div>
            </div>
            <div class="box">
                <div class="box-left" :style="{backgroundImage:`url(http://mapi.fufentong.com${i.left.img})`}">
                    <a :href="i.left.src" target="_blank" style="display: block;height: 100%">
                        <div class="tile" :style="{backgroundColor:i.left.color}"><a :href="i.left.src" target="_blank"
                                                                                     style="font-weight: bold">{{i.left.title}}</a>
                            <img style="width: 18px;
                                    display: block;
                                    position: absolute;
                                    top: 20px;
                                    transform: translateX(200px);
"
                                 src="../assets/img/right_line.png" alt="">
                        </div>
                        <div class="desc" :style="{backgroundColor:i.left.color}"><span class="lager-font bold">{{i.left.desc}}</span></div>
                    </a>
                </div>
                <div class="box-right">
                    <div class="box-right-item fl" :class="{noMargin:(index+1)%4===0}" v-for="(r,index) in i.right">
                        <p><a :href="r.src" target="_blank">{{r.title}}<img class="fr"
                                                                            src="../assets/img/maruright.png"></a></p>
                        <a :href="r.src" target="_blank">
                            <img class="item-img" :src="`http://mapi.fufentong.com${r.img}`" alt="">
                        </a>
                    </div>
                </div>
                <div class="box-bottom" v-if="i.product.length>0">
                    <div class="tile">
                        <i></i>热销商品推荐
                        <a class="fr" @click="randomList(k)">
                            <img src="../assets/img/refresh.png" alt="">
                            换一批看看</a>
                    </div>
                    <div class="box-bottom-item fl" v-for="(b,key) in i.product" v-if="key<5">
                        <a :href="`/goods.html#/${b.product_id}`" target="_blank">
                            <img width="160px" height="160px" :src="b.default_img|img" alt="">
                        </a>
                        <a :href="`/goods.html#/${b.product_id}`" target="_blank" class="link ac">
                            {{b.product_name}}
                        </a>
                        <p class="price ac">
                            {{b|exch(ex)}} <span style="font-size: 14px;color: #333333">{{ex.unit}}</span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "floor",
        data() {
            return {
                ex: {},
                form: []
            }
        },
        created() {
            this.getIndexFloor();
        },
        methods: {
            getIndexFloor() {
                this.yum('api/index/getIndexFloor').then(
                    res => {
                        this.form = res.data.data;
                        this.ex = {
                            exchange: res.data.exchange,
                            has_custom_price: res.data.has_custom_price,
                            unit: res.data.unit
                        }
                    }
                )
            },
            randomsort(a, b) {
                return Math.random()>.5 ? -1 : 1;
                //用Math.random()函数生成0~1之间的随机数与0.5比较，返回-1或1
            },
            randomList(k){
                let object = this.form[k];
                let bottom = this.form[k].product.sort(this.randomsort);
                let data = Object.assign(object,{product:bottom});
                this.$set(this.form,k,data)
            }
        }
    }
</script>

<style scoped lang="stylus">
    .floor
        background-color #f2f2f2

    .tile
        height 60px
        line-height 60px
        position relative
        text-align center
        font-size 22px
        font-weight bold
        user-select none
        img
            width 1190px
            position: absolute;
            left: 0;
            top: 20px;

    .box
        margin 0 auto
        width 1200px
        &-left
            position relative
            width 230px
            height 470px
            margin-right 10px
            display inline-block
            background-color white
            .tile
                position absolute
                width 100%
                height 55px
                line-height 55px
                color white
                padding 0 20px
                font-size 18px
                background-color #038aeb
                a
                    display block
                    color white
            .desc
                position absolute
                width 100%
                height 60px
                line-height 60px
                bottom 0
                opacity 0.6
                background-color #038aeb
                text-align center
                span
                    display inline
                    width 80px
                    height 60px
                    color white
                    opacity 1
        &-right
            width 950px
            height 470px
            display inline-block
            &-item
                width 230px
                height 230px
                margin-right 10px
                margin-bottom 10px
                background-color white
                transition 0.3s
                p
                    font-size 16px
                    margin-top 20px
                    padding 0 20px
                    img
                        width 17px
                img.item-img
                    display block
                    width 140px
                    height 140px
                    margin 18px auto 0
                &.noMargin
                    margin-right 0
                &:hover
                    box-shadow: #ccc 0 0 20px;
                    transform: translate(0px, -3px);
        &-bottom
            width 100%
            height 340px
            .tile
                padding 0 10px 0 0
                height 40px
                line-height 40px
                font-size 16px
                color black
                text-align left
                i
                    vertical-align middle
                    display inline-block
                    margin-right 15px
                    width 5px
                    height 20px
                    background-color #038aeb
                a
                    font-size 14px
                    img
                        position relative
                        display inline-block
                        width 16px
                        top 0
            &-item
                width 230px
                height 300px
                margin-right 10px
                background-color white
                transition 0.3s ease-in-out
                &:last-child
                    margin-right 0
                &:hover
                    box-shadow: #ccc 0 10px 15px;
                    transform: translate(0px, -3px);
                    transition 0.3s ease-in-out
                img
                    margin 35px auto 0
                    display block
                a.link
                    display block
                    width 184px
                    margin 24px auto 0
                    height 20px
                    overflow hidden
                p.price
                    width 184px
                    margin 10px auto 0
                    font-size 18px
                    color #ff0077
                    font-weight bold
</style>