<template>
    <div class="banner">
        <div class="cur-banner">
            <transition-group name="slide" tag="div" class="bannerbox" mode="in-out">
                <a class="bannerimg ac"
                   :href="i.banner_link"
                   v-for="(i,k) in bannerData"
                   :key="k"
                   v-show="k === activeBanner"
                   @mouseenter="clearShift()"
                   @mouseout="bannerShift()"
                ><img class="ac" :src="i.banner_img|img1"></a>
                <ul class="slide-control" key="control">
                    <li class="control-item cursor" :class="{active:key === activeBanner}" v-for="(i,key) in bannerData"
                        @click="activeBanner = key"></li>
                </ul>
            </transition-group>
        </div>
        <div class="container"
        >
            <div class="nav">
                <ul class="nav-conf">
                    <li v-for="(i,k) in nav.child" :key="k" v-show="k<10"><a>{{i.cname}}
                        <img class="fr" src="../assets/img/right.png"></a>
                        <div class="child-nav">
                            <table style="height: 420px ;overflow-y:scroll;display: block">
                                <tr v-for="(o,k) in i.child">
                                    <td class="td-tile cursor" @click="navLink(o.cid)">{{o.cname}}</td>
                                    <td class="child-item">
                                        <a class="c-item" v-for="(c,k) in o.child" :key="k" @click="navLink(c.cid)">{{c.cname}}</a>
                                    </td>
                                </tr>
                            </table>
                            <div class="ad">
                                <a :href="i.banner_link" class="ad-item" v-for="i in brand_pos ">
                                    <img width="100%" height="100%" :src="i.banner_img|img1" alt="">
                                </a>
                                <div class="cat">
                                    <a class="cat-item" v-for="i in cat_pos" :href="i.banner_link">
                                        <span>{{i.banner_name}}</span>
                                        <img :src="i.banner_img|img1" alt="">
                                    </a>
                                </div>
                            </div>
                        </div>
                    </li>
                    <li><a href="/grade.html">更多分类
                        <img style="width: 16px;margin-top: 10px;transform: translateX(4px);" class="fr"
                             src="../assets/img/divide.png"></a></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "BannerToNav",
        components: {},
        data() {
            return {
                nav: {},
                brand_pos: Array,
                cat_pos: Array,
                bannerData: Array,
                activeBanner: 0
            }
        },
        created() {
            this.lists();
            this.rightAdPosition();
            this.getBannerList();
            window.shift = setInterval(() => {
                if (this.activeBanner === 0) {
                    this.activeBanner = this.bannerData.length - 1;
                } else {
                    this.activeBanner -= 1
                }
            }, 5000)
        },
        computed: {},
        methods: {
            clearShift() {
                window.clearInterval(shift)
            },
            bannerShift() {
                window.shift = setInterval(() => {
                    if (this.activeBanner === 0) {
                        this.activeBanner = this.bannerData.length - 1;
                    } else {
                        this.activeBanner -= 1
                    }
                }, 5000)
            },
            getBannerList() {
                this.yum('api/index/getBannerList',{port:2}).then(res => {
                    if (res.data.status === 200) {
                        this.bannerData = res.data.data.bannerData
                    }
                })
            },
            navLink(id) {
                location.href = `/grade.html?limit=20&order=asc&type=3&page=1&cid=${id}`
            },
            lists() {
                this.yum('api/category/lists').then(
                    res => {
                        if (res.data.status === 200) {
                            this.nav = res.data.data[0]
                        }
                    }
                )
            },
            rightAdPosition() {
                this.yum('api/index/rightAdPosition', {types: '21,22'}).then(res => {
                    this.brand_pos = res.data.data.brand_pos;
                    this.cat_pos = res.data.data.cat_pos
                })
            }
        }
    }
</script>

<style scoped lang="stylus">
    .banner
        height 440px
        overflow hidden
        position relative
        .nav
            position absolute
            width 180px
            height 440px
            background-color #262626
            display inline-block
            &-conf
                width 180px
                height 440px
                position relative
                li
                    height 40px
                    line-height 40px
                    padding 0 20px
                    color white
                    a
                        display block
                        color white
                        img
                            margin-top 15px
                    .child-nav
                        display none
                        background-color white
                        position absolute
                        top 0
                        left 180px
                        width 1000px
                        height 440px
                        padding-left 30px
                        .ad
                            position absolute
                            height 440px
                            width 270px
                            padding 0 25px
                            right 0
                            top 0
                            background-color #f2f2f2
                            &-item
                                width 50%
                                height 60px
                                float left
                                &:hover
                                    box-shadow: 0 0 2px #9a9a9a;
                                    transform: translate(-1px, -1px);
                                    transition: 0.2s;
                                img
                                    margin-top 0
                            .cat
                                position absolute
                                bottom 0
                                &-item
                                    position relative
                                    width 218px
                                    height 90px
                                    background-color white
                                    padding 0 20px
                                    span
                                        font-size 16px
                                        font-weight bold
                                    img
                                        width 70px
                                        height 70px
                                        position absolute
                                        right 10px
                                        bottom 10px
                        table
                            width 680px
                            td
                                padding-top 10px
                            a.c-item
                                display inline-block
                                font-size 13px
                                margin-right 20px
                                &:hover
                                    color #ff0077
                            td.td-tile
                                display inline-block
                                width 100px
                                overflow hidden
                                white-space nowrap
                                text-overflow ellipsis
                                font-size 13px
                                font-weight bold
                                vertical-align top
                            td.child-item
                                border-bottom 1px solid #ccc
                    &:hover
                        background white
                        color black
                        .child-nav
                            display block
                        a
                            display block
                            color black
                    &:last-child
                        width 180px
                        height 40px
                        line-height 40px
                        position absolute
                        bottom 0
        .cur-banner
            position absolute
            width 100%
            min-width 1200px
            height 440px
            background-position center
            background-size cover
            background-repeat no-repeat
            overflow hidden
            text-align center
            .slide-control
                height 8px
                position absolute
                right 50%
                bottom 30px
                li.control-item
                    display inline-block
                    width 16px
                    height 16px
                    background white
                    margin-right 5px
                    border-radius 8px
                    &.active
                        background #0d90ff
            div.bannerbox
                width 1200px
                height 440px
                position relative
                margin 0 auto
            a.bannerimg
                display inline-block
                width 1200px
                position absolute
                left 0
            img
                width 1920px
                margin 0 auto
                transform translateX(-360px)
</style>
