<template>
    <div class="recommend" v-if="recommend.length>0">
        <div class="container">
            <div class="tile">
                <img v-if="type ==='today'" src="../assets/img/recommend.png" alt="">
                <img v-if="type ==='newProduct'" src="../assets/img/newgoods.png" alt="">
                <img v-if="type ==='YouLike'" src="../assets/img/like.png" alt="">
            </div>
            <div class="recommend-box clearfix" @mouseenter="removeInterval"
                 @mouseleave="addInterval">
                <img v-if="type === 'today'" @click="randomRecommend" class="before-left random"
                     src="../assets/img/btn-left.png" alt="">
                <transition-group name="fade" class="clearfix" mode="out-in" :css="true">
                    <div class="item fl" v-for="(i,k) in recommend" v-if="k<num"
                         :style="{marginRight:(k+1)%5===0?'0px':'10px'}"
                        :key="k"
                    >
                        <a :href="`/goods.html#/${i.product_id}`" target="_blank"><img :src="i.default_img|img" alt=""></a>
                        <a class="title" :href="`/goods.html#/${i.product_id}`" target="_blank">{{i.product_name}}</a>
                        <p class="price">{{i|exch(ex)}} <span style="font-size: 14px;color: #333333">{{ex.unit}}</span></p>
                    </div>
                </transition-group>
                <img v-if="type === 'today'" @click="randomRecommend" class="before-right random"
                     src="../assets/img/btn-lright.png" alt="">
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "recommend",
        props: {
            num: Number,
            type: String//根据父组件传参判断请求哪个列表
        },
        data() {
            return {
                recommend: [],
                recommendList: [],
                ex: null
            }
        },
        created() {
            if (this.type === 'today') {
                this.dailyRecommendation();
                window.interval = setInterval(()=>{
                    this.randomRecommend()
                },3000)
            }
            if (this.type === 'newProduct') {
                this.newProductList()
            }
            if (this.type === 'YouLike') {
                this.guessYouLike()
            }
        },
        methods: {
            removeInterval(){
                window.clearInterval(interval)
            },
            addInterval(){
                if (this.type === 'today'){
                    window.interval = setInterval(()=>{
                        this.randomRecommend()
                    },3000)
                }
            },
            randomsort() {
                return Math.random()>.5 ? -1 : 1;
                //用Math.random()函数生成0~1之间的随机数与0.5比较，返回-1或1
            },
            randomRecommend() {
                this.recommend = this.recommendList.sort(this.randomsort)
            },
            //今日推荐
            dailyRecommendation() {
                this.yum('api/ai/dailyRecommendation').then(res => {
                    if (res.data.status === 200) {
                        this.recommendList = res.data.data;
                        this.ex = {
                            exchange: res.data.exchange,
                            has_custom_price: res.data.has_custom_price,
                            unit: res.data.unit
                        };
                        this.randomRecommend()
                    }
                })
            },
            //新品推荐列表
            newProductList() {
                this.yum('api/index/newProductList').then(res => {
                    this.recommendList = res.data.data;
                    this.recommend = res.data.data;
                    this.ex = {
                        exchange: res.data.exchange,
                        has_custom_price: res.data.has_custom_price,
                        unit: res.data.unit
                    };
                })
            },
            //猜你喜欢
            guessYouLike() {
                this.yum('api/ai/guessYouLike').then(
                    res => {
                        this.recommendList = res.data.data;
                        this.recommend = res.data.data;
                        this.ex = {
                            exchange: res.data.exchange,
                            has_custom_price: res.data.has_custom_price,
                            unit: res.data.unit
                        }
                    }
                )
            }
        }
    }
</script>

<style scoped lang="stylus">
    .recommend
        background-color #f2f2f2
        .container
            padding-left 0px
    .tile
        height 60px
        line-height 60px
        img
            width 1190px
    .recommend-box
        height auto
        position relative
        .random
            position absolute
            cursor pointer
        .before-left
            top 100px
            left -55px
        .before-right
            right -55px
            top 100px
        .item
            height 300px
            width 230px
            margin-bottom 10px
            background-color white
            transition: all .3s;
            &:last-child
                margin-right 0
            &:hover
                box-shadow: #ccc 12px 12px 25px;
                transform: translate(0px, -5px);
            a > img
                display block
                width 160px
                height 160px
                margin 36px 34px 25px
            a.title
                display block
                width 184px
                height 38px
                max-width 184px
                max-height 38px
                overflow hidden
                text-align center
                margin 0 auto
            p.price
                text-align center
                font-size 18px
                color #ff0077
                font-weight bold
                margin-top 8px

</style>