<template>
    <transition name="slide">
        <div class="fixedSearch full-width" v-show="showbar">
            <div class="container">
                <div class="search-class">
                    <img src="../../src/assets/img/fixedlogo.png" alt="logo">
                    <input type="text" :style="{borderColor:'#ff0077'}" v-model="keyword" @keyup.enter="search()">
                    <button :style="{backgroundColor:'#ff0077'}" @click="search()">搜索</button>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    export default {
        name: "fixedSearch",
        data() {
            return {
                keyword: '',
                showbar: false
            }
        },
        mounted() {
            window.addEventListener('scroll', () => {
                if (window.timer) {
                    clearTimeout(timer)
                }
                window.timer = setTimeout(() => {
                    if (document.documentElement.scrollTop > 600) {
                        this.showbar = true
                    } else {
                        this.showbar = false
                    }
                }, 100)
            })
        },
        methods: {
            search() {
                location.href = `/grade.html?limit=20&order=asc&type=3&page=1&keyword=${this.keyword}&token`
            }
        }
    }
</script>

<style scoped lang="stylus">
    .fixedSearch
        position fixed
        top 0
        left 0
        height 55px
        line-height 55px
        z-index 1
        background-color white
        box-shadow 0 0 10px #aaa
        .search-class
            position relative
        input
            position absolute
            bottom 7px
            height 40px
            width 625px
            outline none
            border 2px solid
            padding-left 10px
            margin-left 200px
        button
            position absolute
            bottom 7px
            margin-left 825px
            height 40px
            line-height 40px
            width 135px
            color #fff
            border-style none
            font-size 17px
            font-weight bold
            outline none
            cursor pointer

    .slide-enter-active, .slide-leave-active {
        transition: .3s;
        transform translateY(0px)
    }

    .slide-enter, .slide-leave-to {
        /*opacity: 0;*/
        transform translateY(-60px)
    }
</style>